/*加载动画*/
.spinner {
  position: fixed;
  left: 40%;
  bottom: 10%;
  width: 80px;
  height: 80px;
  margin: 50px auto;
  background-color: #69c61d;
  border-radius: 100%;
  -webkit-animation: scaleout 1s infinite ease-in-out;
  animation: scaleout 1s infinite ease-in-out;
}
@-webkit-keyframes scaleout {
0% {
    -webkit-transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
    opacity: 0;
}
}
.goTop {
  color: #50bfff;
  position: fixed;
  bottom: 40px;
  right: 20px;
  z-index: 9999;
  cursor: pointer;
  width: 60px;
  height: 60px;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  -webkit-transform: scale(0.6);
          transform: scale(0.6);
}
.goTop:active {
  color: #c40000;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
